/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
  font-family: "Inter", sans-serif;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

/** UI **/
.header {
  background-color: #a31a13;
  height: auto;
  left: 0px;
  position: fixed;
  right: 0px;
  top: 0px;
  z-index: 10;
  display: block;
}

.contenedor-header {
  border-width: 0px 0px 0.3rem 0px;
  border-color: #900a0a;
  border-style: solid;
  width: 100%;
  transform: none;
  transform-origin: 50% 50% 0px;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: flex-start;
  padding: 1.8rem 0;
  flex-wrap: nowrap;
  height: min-content;
  position: relative;
}

.barra {
  transform: none;
  transform-origin: 50% 50% 0px;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  max-width: unset;
  width: 100%;
  flex-wrap: nowrap;
  gap: 2.4rem;
  height: min-content;
  justify-content: flex-start;
  padding: 0 2.4rem;
  position: relative;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    align-items: center;
  }
}

.barra-arriba {
  transform: none;
  transform-origin: 50% 50% 0px;
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  height: min-content;
  padding: 0;
  position: relative;
}

.barra-img {
  transform: none;
  transform-origin: 50% 50% 0px;
  height: auto;
  position: relative;
  width: auto;
  display: block;
  unicode-bidi: isolate;
}

.mobile-menu {
  transform: none;
  transform-origin: 50% 50% 0px;
  height: 4rem;
  position: relative;
  width: 4rem;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}

.mm1 {
  height: 100%;
  width: 100%;
  transform: none;
  transform-origin: 50% 50% 0px;
  aspect-ratio: 1/1;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  gap: 1rem;
  cursor: pointer;
  justify-content: center;
  padding: 0;
  position: relative;
}

.mm2 {
  transform: none;
  transform-origin: 50% 50% 0px;
  position: relative;
  height: 3.6rem;
  width: 3.6rem;
}
.mm2 img {
  height: 3.6rem;
  width: 3.6rem;
}

.barra-abajo {
  transform: none;
  transform-origin: 50% 50% 0px;
  display: none;
  align-items: flex-start;
  flex-direction: column;
  gap: 2.4rem;
  justify-content: flex-start;
  width: 100%;
  flex-wrap: nowrap;
  height: min-content;
  padding: 0;
  position: relative;
  z-index: 8;
}
@media (min-width: 768px) {
  .barra-abajo {
    transform: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
  }
}

.mostrar {
  display: flex !important;
}

.video {
  text-align: left;
  color: #ffffff;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  background-color: rgba(156, 8, 0, 0.883);
  flex: 0 0 auto;
  height: auto;
  position: relative;
  margin-bottom: 0;
  width: 100%;
  opacity: 1;
}

.contenedor-footer {
  opacity: 1;
  width: 100%;
  opacity: 1;
  padding: 4rem 2.4rem 3.2rem;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  flex-wrap: nowrap;
  gap: 4.8rem;
  height: min-content;
  justify-content: center;
  overflow: hidden;
  position: relative;
}

.footer-arriba {
  opacity: 1;
  gap: 4rem;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  flex-wrap: nowrap;
  height: min-content;
  justify-content: flex-start;
  overflow: visible;
  padding: 0;
  position: relative;
  width: 100%;
}

.footer-img {
  flex-shrink: 0;
  opacity: 1;
  flex: none;
  width: 20rem;
  position: relative;
}

/** copyright **/
.footer-abajo {
  opacity: 1;
  display: flex;
  align-items: flex-start;
  align-content: flex-start;
  flex-direction: column;
  flex-wrap: nowrap;
  height: min-content;
  justify-content: flex-start;
  overflow: visible;
  padding: 0;
  position: relative;
  width: 100%;
  z-index: 8;
}

.contenedor-copyright {
  outline: none;
  display: flex;
  flex-direction: column;
  justify-content: start;
  flex-shrink: 0;
  transform: none;
  opacity: 1;
  order: 1;
  height: auto;
  position: relative;
  user-select: none;
  white-space: pre-line;
  width: auto;
}

.copyright {
  color: #e39589;
  text-align: left;
  font-size: 12px;
  margin: 0;
  padding: 0;
}
.copyright a {
  color: #fffcfc;
  box-sizing: border-box;
  cursor: pointer;
  font-weight: 400;
  letter-spacing: normal;
  line-height: 1.6rem;
  text-align: start;
  text-decoration: underline !important;
  text-decoration-line: underline;
  text-decoration-color: #e39589;
  text-decoration-style: solid;
  text-decoration-thickness: 0.1rem;
  text-transform: none;
  text-underline-offset: 5px;
  text-wrap-mode: nowrap;
  text-wrap-style: auto;
  transition-behavior: normal;
  transition-delay: 0s;
  transition-duration: 0.3s;
  transition-property: color;
  transition-timing-function: cubic-bezier(0.12, 0.23, 0.5, 1);
  white-space-collapse: preserve;
}
.copyright a:hover {
  color: #FF9080;
  text-decoration: none !important;
}
.copyright a::selection {
  background-color: #9F140F;
}

/** Parte header ⬆️ **/
.c-nav {
  transform: none;
  transform-origin: 50% 50% 0px;
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  flex-wrap: nowrap;
  gap: 0.4rem;
  height: min-content;
  justify-content: flex-start;
  padding: 0;
  position: relative;
}
@media (min-width: 768px) {
  .c-nav {
    flex-direction: row;
    justify-content: flex-end;
  }
}

.c1-nav {
  transform: none;
  transform-origin: 50% 50% 0px;
  display: flex;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  gap: 1rem;
  height: min-content;
  justify-content: center;
  padding: 0;
  position: relative;
  width: min-content;
}

.c2-nav {
  transform: none;
  transform-origin: 50% 50% 0px;
  display: block;
  height: 3.2rem;
  position: relative;
  width: auto;
}
.c2-nav a {
  display: flex;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  gap: 0.4rem;
  justify-content: center;
  height: 100%;
  padding: 0.8rem 1.2rem;
  position: relative;
  width: min-content;
}

.c3-nav {
  outline: none;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex-shrink: 0;
  transform: none;
  transform-origin: 50% 50% 0px;
  opacity: 1;
  height: auto;
  position: relative;
  white-space: pre;
  width: auto;
}
.c3-nav p {
  font-size: 1.4rem;
  line-height: 1.6rem;
  margin: 0px;
  padding: 0px;
  color: #fffcfc;
  font-weight: 700;
}
.c3-nav p:hover {
  color: #FF9080;
}
.c3-nav p::selection {
  background-color: #E39589;
}

/** Parte footer ⬇️ **/
.c-nav-f {
  display: flex;
  opacity: 1;
  flex-direction: column;
  gap: 3.2rem;
  width: 100%;
}
@media (min-width: 768px) {
  .c-nav-f {
    align-items: flex-start;
    flex-direction: row;
    flex-wrap: nowrap;
    gap: 8rem;
    height: min-content;
    justify-content: flex-start;
    padding: 0;
    position: relative;
  }
}

.c1-nav-f {
  opacity: 1;
