}
.t-nav-f p {
  color: #e39589;
  font-size: 1.8rem;
  font-weight: 700;
  line-height: 1.6rem;
  margin: 0px;
  padding: 0px;
}
.t-nav-f p::selection {
  background-color: #c43e32;
  color: #e39589;
}

.c2-nav-f {
  outline: none;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex-shrink: 0;
  transform: none;
  opacity: 1;
  height: auto;
  position: relative;
  width: auto;
}
.c2-nav-f p {
  font-size: 1.4rem;
  line-height: 1.6rem;
  margin: 0px;
  padding: 0px;
}
.c2-nav-f p a {
  color: #e39589;
  box-sizing: border-box;
  cursor: pointer;
  font-weight: 400;
  letter-spacing: normal;
  line-height: 1.6rem;
  text-align: start;
  text-wrap-mode: nowrap;
  text-wrap-style: auto;
  transition-behavior: normal;
  transition-delay: 0s;
  transition-duration: 0.3s;
  transition-property: color;
  transition-timing-function: cubic-bezier(0.12, 0.23, 0.5, 1);
  white-space-collapse: preserve;
}
.c2-nav-f p a:hover {
  text-decoration-line: underline !important;
  text-decoration-color: #e39589;
  text-decoration-style: solid;
  text-decoration-thickness: 0.1rem;
  text-transform: none;
  text-underline-offset: 5px;
