/**  **/
.mostrar {
  display: flex;
}

.contenedor {
  position: relative;
  overflow: hidden;
  display: block;
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
  max-width: 100% !important;
  padding-top: 10rem;
}

.video {
  position: relative;
  max-width: 100%;
  min-height: 100%;
  overflow: hidden;
  object-fit: cover;
}
.video .overlay {
  position: absolute;
  background: linear-gradient(270deg, rgba(52, 58, 64, 0.6), rgba(6.275862069, 7, 7.724137931, 0.9));
  inset: 0;
  display: flex;
  align-items: center;
}
.video video {
  min-width: 100%;
  min-height: 100vh;
  max-width: 100%;
  max-height: 100vh;
  object-fit: cover;
  display: block;
  width: auto;
}
@media (min-width: 1024px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  padding: 0.8rem 0;
  z-index: 5;
}
.contenido-video h1 {
  padding-left: 1.6rem;
  display: flex;
  text-align: start;
  font-weight: 700;
  font-size: 7rem;
  margin: 0;
  display: block;
}
.contenido-video p {
  color: #ffffff;
  padding: 1rem 2rem;
  font-weight: 400;
  font-size: 1.5rem;
  margin: 0;
  display: block;
}

.services {
